header.site-header {
	background-color: white;	
	border-bottom: 1px solid rgba(50,50,50,.15);
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);	
	height: 90px;
	position: sticky;
	top: 0;
	width: 100%; 
	z-index: 5;

	.site-branding {
		align-items: center;
		display: flex;
		justify-content: space-between;
		margin: auto;
		max-width: @inner;
		
		//logo 
		img{
			height: 80px;
			width: auto;
			max-width: 80px;		
		}

		//links ( basically the social media links)
		a {
			border-bottom: 0;
			color: #7A7A7A;
			&:hover{ color: @link-color; }
		}
	
	}


	@media( max-width: @inner ) {
		.site-branding {
			padding: 0 1em;
		}
	}

	@media( max-width: @inner-inner ) {
		header.site-header {
			height: 150px;
		}
		.site-branding {
			flex-direction: row-reverse;
			flex-wrap: wrap;
			justify-content: center;	
			padding: 0;		
		}

		.site-branding {
			.nav-toggle, a[rel="home"] {
				text-align: center;
				width: 50%;
			}
			ul.sharing-links {
				background: lighten( @complementary, 10% );
				padding: .5em 0;
				text-align: center;
				width: 100%;
				a {
					color: @analogous1;
					&:hover {
						color: @analogous2;
					}
				}
			}
		}

	}

	.nav-toggle {
		cursor: pointer;
		font-size: 2em;
	}

	ul.sharing-links {
		margin: 0;
		padding: 0;

		li {
			display: inline-block;
			margin: 0 .5em;
		}

		span.fa{
			font-size: 1.5em;
		}

	}

}





